.backDrop {
  position: fixed;
  z-index: 10;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(3px);
}

.modalContainer {
  position: relative;
  width: 500px;
  padding: 30px 15px 15px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 3px 3px 10px #000000;
}

.closeBtn {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  border: 2px solid #ff9500;
  border-radius: 50%;
  background-color: #ffffff;
  transition: background-color 250ms;
}
.closeBtn::before, .closeBtn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 80%;
  height: 2px;
  background-color: #ff9500;
  transition: background-color 250ms;
}
.closeBtn::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.closeBtn::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.closeBtn:hover, .closeBtn:focus {
  background-color: #ff9500;
}
.closeBtn:hover::before, .closeBtn:hover::after, .closeBtn:focus::before, .closeBtn:focus::after {
  background-color: #ffffff;
}